<script>
import { Url, widowSize } from "../components/public";
export default {
  name: "Home",
  data() {
    return {
      arrAdv: [],
      arrNotice: [],
      height: "",
      arr: [],
      timer: null,
      arrResult: []
    };
  },
  mounted() {
    // const data = sessionStorage.getItem("beijing");
    // if (data) {
    //   this.arr = JSON.parse(data);
    //   this.proc();
    // } else {
      this.$axios.get("getData").then(arr => {
        this.arr = arr;
        // sessionStorage.setItem("beijing", JSON.stringify(arr));
        this.proc();
      });
    // }
  },
  methods: {
    marquee() {
      const article = this.$refs.marquee;
      this.timer = setInterval(_ => {
        if (article.scrollTop >= 328) {
          article.scrollTop = 0;
        } else article.scrollTop++;
      }, 30);
    },
    stop() {
      clearInterval(this.timer);
    },
    proc() {
      const arr = this.arr;
      let temp = arr[0];
      temp = temp.map(el => {
        el.src = Url + "adv/" + el.src;
        return el;
      });
      this.arrAdv = temp;
      this.height = (widowSize().width / 1920) * 575 + "px";
      arr.splice(0, 1);
      this.arrNotice = arr[4];
      this.marquee();
      arr.splice(4, 1);
      arr.forEach((subArr, i) => {
        subArr = subArr.map(el => {
          let imgArr = el.content.match(/src="[^"]+/);
          el.src = imgArr[0].substr(5);
          return el;
        });
        if (i > 0 && i < 3) {
          subArr = subArr.map(el => {
            el.text = el.content.match(/<p>[^>]+>/)[0];
            return el;
          });
        }
      });
      this.arrResult = arr;
    }
  },
  filters: {
    format(d) {
      return d.replace(/\//g, "-");
    }
  }
};
</script>
